html{
	background: #000;
}

body{
	font-family: 'Permanent Marker', cursive;
	background: url(../img/bg.png) center no-repeat;
	background-size: cover;
	height: 100vh;
	width: 100%;
	color: #464e32;
}	 

.wrapper{
	padding: 5%;
}

header{
	border: 5px solid #750000;
	border-radius: 10px;
	text-align: center;
}

h1{
	font-size: 50px;
	text-shadow: 4px 4px 4px #25291d;
	padding: 20px 0;
}

h2{
	font-size: 25px;
	padding-bottom: 20px;
	text-shadow: 2px 2px 2px #25291d;
}

.wrapper div{
	margin: 50px 5%;
	text-shadow: 2px 2px 2px #25291d;

}

h3{
	font-size: 25px;
	text-decoration: underline;
	margin-bottom: 20px;
}

ul{
	font-size: 20px;
	line-height: 24px;
}

